body{
  background-color: black;
}
.to-do-list{
  font-family: Arial, sans-serif;
  text-align: center;
  margin-top: 100px;
}
h1{
  font-size: 4rem;
  color: aliceblue;
}
button{
  font-size: 1.7rem;
  font-weight: bold;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.5 ease;
}
.add-button{
  background-color: hsl(125 , 47% , 54%);
}
.add-button:hover{
  background-color: hsl(125,47%,44%);
}
.delete-button{
  background-color: hsl(10,90%,50%);
}
.delete-button:hover{
  background-color: hsl(10,90%,40%);
}
.edit-button{
  background-color: hsl(207,90%,64%);
}
.edit-button:hover{
  background-color: hsl(207,90%,54%);
}
input[type='text']{
  font-size: 1.6rem;
  padding: 10px;
  border:2px solid hsla(0,0%,80%,0.5);
  border-radius: 5px;
  color: hsla(0, 0%, 0% , 0.5);
}
ol{
  padding: 0;
}
li{font-size:2rem;
  font-weight: bold;
  padding: 15px;
  background-color: hsl(0,0%,97%);
  border: 3px solid hsl(0,0%,0%);
  border-radius: 5px;
  display: flex;
  align-items: center;
}
.text{
  flex: 1;
}
.delete-button, .edit-button{
  padding: 8px 12px;
  font-size: 1.4rem;
  margin-left: 10px;
}